 *, *:after, *:before {
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
	font-family: 'Poppins', sans-serif;
}

.gameContainer  {
	height: 100%;
	position:relative;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

.cardContainer {
	position: absolute;
	height: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.cardContainer h3 {
	font-size: 1.5rem;
	font-weight: 700;
	color: #fff;
	position: relative;
	top: 10px;
}

.title {
	width: 230px;
	max-width: 100%;
}

.theGame {
	position: relative;
	padding: 10px;
	padding-top: 18px;
	display: flex;
	flex-flow: row wrap;
	justify-content: center;
	background: linear-gradient(90deg, rgba(211,216,228,1) 0%, rgba(238,236,243,1) 50%, rgba(211,216,228,1) 100%);
	border: 4px solid #fff;
	border-radius: 10px;
}

.total, .timer {
	width: 160px;
	max-width: 100%;
	position: relative;
	text-align: center;
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 15px;
}

.move-count, .timer-container  {
	color: white;
	font-size: 15px;
	font-weight: 700;
	position: absolute;
	top: 30px;
}


.move-count p span, .timer-wrapper span {
	position: relative;
	top: 5px;
	font-size: 35px;
	color: #525252;
}


