.card-wrapper {
	width: 85px;
	height: 85px;
	transform-style: preserve-3d;
	display: inline-block;
	margin: 0.5rem;
	perspective: 1000px;
	transition: all 1s ease;
}
.card-container {
	width: 100%;
	height: 100%;
	position: relative;
	transition: transform 1s;
	transform-style: preserve-3d;
}
.game-card {
	color: #555;
	cursor: pointer;
	user-select: none;
	position: absolute;
	padding: 0.5rem;
	width: 100%;
	height: 100%;
	overflow: hidden;
	-webkit-backface-visibility: hidden;
	backface-visibility: hidden;
	border-radius: 0.5rem;
	box-shadow: 5px 5px 10px 0 rgba(90, 116, 148, 0.3);
}
.game-card svg {
	width: 100%;
	height: 100%;
}
.card-back {
	background-image: url(../img/card-back.svg);
	position: absolute;
	z-index: 1;
}
.card-front {
	background-image: url(../img/card-front.svg);
	transform: rotateY(180deg);
}
.is-flipped {
	transform: rotateY(180deg);
}
.is-matched {
	animation: scale 0.4s cubic-bezier(0.86, 0, 0.07, 1);
	animation-delay: 1s;
}
.is-reset {
	animation: scaleBack 0.4s cubic-bezier(0.86, 0, 0.07, 1);
}
.is-stacked {
	margin-bottom: -170px;
	margin-right: -160px;
}
.dots {
	top: 0;
	left: 0;
	right: 0;
	margin: auto;
	height: 100%;
	overflow: hidden;
}
.dots > * {
	border-radius: 50px;
