#modal {
	position: fixed;
	background-image: url(../img/winBg.svg);
	background-size: cover;
	background-position: center;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	z-index: 999;
	pointer-events: none;
	transition: all 0.3s;
	display: none;
	align-items: center;
	justify-content: center;
}

#modal svg {
	width: 80%;
	position: relative;
	z-index: 99;
}
#modal button {
	margin-bottom: 2rem;
	font-size: 1.5rem;
}
#modal.show {
	display: flex;
	pointer-events: auto;
}

#modal h2 {
	color: #5e5aaf;
	font-size: 1.2rem;
	font-weight: normal;
	margin-bottom: 3rem;
}
.svg-box {
	position: relative;
}
.background-blur {
	position: absolute;
	top: 30%;
	left: 20%;
	width: 60%;
	height: 40%;
	border-radius: 50%;
	filter: blur(3rem);
}

.modal-box {
	position: absolute;
	background-color: #f9f8fe;
	box-shadow: 5px 5px 20px 0 rgba(69, 61, 132, 0.3);
	border-radius: 10px;
	width: 80%;
	max-width: 500px;
	text-align: center;
	padding: 2rem;
	animation: jitter 2.5s forwards ease-in;
}
@keyframes scale {
	0%, 100% {
		transform: scale(1) rotateY(180deg);
}
	50% {
		transform: scale(1.1) rotateY(180deg);
}
}
@keyframes scaleBack {
	0%, 100% {
		transform: scale(1);
}
	50% {
		transform: scale(1.1);
}
}
@keyframes jitter {
	0% {
		transform: scale(0);
}
	10%, 50% {
		transform: scale(1);
}
	15% {
		transform: scale(0.9);
}
	20% {
		transform: scale(1.1);
}
	25% {
		transform: scale(1.1) rotate(-4deg);
}
	30% {
		transform: scale(1.1) rotate(4deg);
}
	35% {
		transform: scale(1.1) rotate(-3deg);
}
	40% {
		transform: scale(1.1) rotate(3deg);
}
	45% {
		transform: scale(1.1) rotate(0);
}
}
